	<view class="popup" :style="'padding-top:' + bar + 'px;'">
		<slot></slot>
	</view>
</template>

<script>
export default {
	props: {
		pclass:{
			default:''
		},
		pstyle:{
			default:''
		},
	},
	data() {
		return {
			bar: uni.getSystemInfoSync().statusBarHeight
		};
	},
	methods: {
	}
};
</script>

